import MUIDataTable from "mui-datatables";
import React, { useState, useEffect } from "react";
import { columns, errorCols, errorOptions } from "./data_header_table";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { requesicoes } from "../../../utils/services";
import Checkbox from '@material-ui/core/Checkbox';
import { NavLink } from "react-router-dom";
import { Fab } from "@material-ui/core";
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import authService from "../../../utils/services/auth";
import { MoonLoader } from 'react-spinners';
import './card.css';


let usuario = null
let dpt = null
let destacar = null
export default function MuiDatatableExample() {
  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableHeadCell: {
          fixedHeader: { backgroundColor: "darkblue", color: "white" }
        },
        MUIDataTableSelectCell: {
          headerCell: { backgroundColor: "darkblue" },
          checkboxRoot: { color: "white", filter: "brightness(0.5)" },
          expandDisabled: {
            // Soft hide the button.
            visibility: "hidden"
          }
        }
      }
    });
  const customRowRender = (rowData, dataIndex, dataIndexMeta) => {
    destacar = calcularDestaque(rowData);
    console.log('destacar ', destacar);
    return (

      <TableRow>
        <TableCell padding="checkbox"> {/* Adicione esta célula para a coluna de seleção */}
          <Checkbox color="primary" /> {/* Adicione um Checkbox (ou qualquer componente de seleção que você está usando) */}
        </TableCell>
        {rowData.map((item, index) => (
          <TableCell key={index} style={{ color: destacar ? 'red' : 'inherit' }}>{item}</TableCell>
        ))}
      </TableRow>
    );
  };

  const [, setIsFormValid] = useState(false);
  const [updateDom, setUpdateDom] = useState(false);
  const [carregando, setCarregando] = useState(true);
  const [professores, setProfessores] = useState([]);

  useEffect(() => {
    usuario = authService.getCurrentUser();
    usuario.authorities[0] === 'DPT' && usuarioLagado()
    setTimeout(() => {
      setCarregando(false);
    }, 5000);
    init()

  }, [])

  async function usuarioLagado() {
    setCarregando(true);
    const usuarioDpt = await requesicoes().getUsuarioLogado(usuario.sub, '?campos=id,numero')
    dpt = usuarioDpt.data
    setCarregando(false)
    setUpdateDom(!updateDom);
  }
  async function init() {
    setCarregando(true);
    const listaProfessores = await Promise.resolve(requesicoes().buscarProfessor('?campos=id,estado,nome,anoAcademico,apelido,regime,nivelAcademico,cadeiras.id,cadeiras.cargaHoraria,cadeiras.nome,cursos.nomeCurso'));
    if (usuario.authorities[0] === 'ADMIN' || usuario.authorities[0] === 'PLAN_N1' || usuario.authorities[0] === 'PLANIFICACAO') {
      const novosProfessores = listaProfessores.data.map((p, i) => {
        const cargaHorariaTotal = p.cadeiras.reduce((total, cadeira) => total + cadeira.cargaHoraria, 0);
        const cargaHoraMensal = cargaHorariaTotal * 4;
        return {
          ...p,
          NO: i + 1,
          nome: p.nome + ' ' + p.apelido,
          nivelAcademico: p.nivelAcademico,
          anoAcademico: p.anoAcademico,
          disciplinas: p.cadeiras.map(c => (c.nome)).join(', '),
          cargaHoraria: cargaHorariaTotal,
          cargaHoraMensal: cargaHoraMensal
        }
      });
      setProfessores(novosProfessores);
      setUpdateDom(!updateDom);
      setCarregando(false)


    } if (usuario.authorities[0] === 'DPT') {
      console.log('Entrou aqui porque eh Dpt', usuario.authorities[0]);

      const listaProfessores = await Promise.resolve(requesicoes().buscarProfessorByDpt(`/${dpt.dpt.id}?campos=id,estado,nome,anoAcademico,apelido,regime,nivelAcademico,cadeiras.id,cadeiras.nome,cadeiras.cargaHoraria,cursos.nomeCurso`));
      const novosProfessores = listaProfessores.data.map((p, i) => {
        const cargaHorariaTotal = p.cadeiras.reduce((total, cadeira) => total + cadeira.cargaHoraria, 0);
        const cargaHoraMensal = cargaHorariaTotal * 4;
        return {
          ...p,
          NO: i + 1,
          nome: p.nome + ' ' + p.apelido,
          nivelAcademico: p.nivelAcademico,
          anoAcademico: p.anoAcademico,
          disciplinas: p.cadeiras.map(c => (c.nome)).join(', '),
          cargaHoraria: cargaHorariaTotal,
          cargaHoraMensal: cargaHoraMensal
        }
      })
      setProfessores(novosProfessores);
      setUpdateDom(!updateDom);
      setCarregando(false);

    }

  }

  const options = {
    // filterType: "checkbox",
    responsive: "standard",
    elevation: 10,
    print: true,
    selectToolbarPlacement: "true",
    selectableRowsOnClick: true,
    expandableRows: false,
    expandableRowsHeader: false,
    customRowRender,
    isRowExpandable: (dataIndex, expandedRows) => {
      const { errors } = professores[dataIndex];
      return errors !== null && errors !== undefined;
    },
    onRowExpansionChange: (curExpanded, allExpanded, rowsExpanded) =>
      console.log(curExpanded, allExpanded, rowsExpanded),
    renderExpandableRow: (rowData, rowMeta) => {

      return (
        <div
          style={{
            marginLeft: "90%",
            width: "100%",
            marginTop: "3%",
            marginBottom: "3%"
          }}
        >
          <MUIDataTable
            columns={errorCols}
            tableData={professores[5]}
            options={errorOptions}
          />
        </div>
      );
    },
    customToolbarSelect: (selectedRows, displayData, setSelectedRows) => {
      console.log(selectedRows, displayData);
      return <div> am youre toolbar header </div>;
    },
    nextPath(path) {
      this.props.history.push(path);
    },
    customToolbar: () => {

      return (
        <div>
          {usuario === 'ADMIN' ? <Fab component={NavLink} to="/teacher/teacher" variant="extended" size="medium"
            style={{
              position: 'fixed',
              bottom: '60px', zIndex: 500,
              right: '15px'
            }} color="primary">Novo</Fab> : ''}


        </div>

      );
    },
    onRowSelectionChange: (
      currentRowsSelected,
      allRowsSelected,
      rowsSelected
    ) => {
      if (
        allRowsSelected !== null &&
        allRowsSelected !== undefined &&
        allRowsSelected.length > 0
      ) {
        setIsFormValid(true);
      } else {
        setIsFormValid(false);
      }
      console.log(currentRowsSelected, allRowsSelected, rowsSelected);
      rowsSelected.map((item) => console.log(professores[item]));
    },

  };
  const calcularDestaque = (rowData) => {
    const cargaHorariaTotal = rowData[6]; // 6 é o índice da coluna 'cargaHoraria'
    const nivelAcademico = rowData[1]; // 1 é o índice da coluna 'nivelAcademico'
    return cargaHorariaTotal > 3 && nivelAcademico === 'Licenciado';
  };

  return (
    <div>

      {carregando ? (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
          <MoonLoader color="#36d7b7" />
        </div>

      ) : (
        <div>
          <MuiThemeProvider theme={getMuiTheme()}>
            <MUIDataTable
              title={'Listagem de Docentes'}
              columns={columns(professores)}
              data={professores}
              options={options}
            />
          </MuiThemeProvider>
          
         <div style={{ color: 'red', marginTop: '10px' }}>
            *Os destacados a vermelho são professores efectivos com carga horária total maior que 12 H/S para Licenciado 10H para Mestres .
          </div>
        </div>
      )}


    </div>
  );
}
